footer {
    background: $gray_02;
    padding: 8px 0;

    .cont {
        max-width: 1630px;
        width: 100%;
        margin: 0 auto;
        padding: 0 15px;

        .footer-content {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @include media ("<phone") {
                flex-direction: column;
            }

            p,
            a {
                color: #fff;
                font-size: 14px;

                @include media ("<phone") {
                    margin-bottom: 5px;
                }
            }

            a {
                transition: all 0.3s ease;

                &:hover {
                    color: $yellow;
                }
            }

            .logo {
                @include media ("<phone") {
                    margin: 20px 0;
                }
            }

            .logo-link {
                margin: 0 3px;

                @include media ("<phone") {
                    margin: 0 15px;
                }

                svg {
                    fill: #fff;
                    transition: all 0.3s ease;
                    object-fit: contain;

                    &:hover {
                        fill: $yellow;
                    }
                }
            }
        }
    }
}